import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { RequestOptions, Headers } from '@angular/http';

import { AuthHttp, tokenNotExpired, JwtHelper } from 'angular2-jwt';


import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs/observable';

import { User } from '../interfaces';

@Injectable()
export class UsersService {

  public api = 'users/';
  private headers = new Headers({'Content-type': 'application/json'});
  private options = new RequestOptions({headers: this.headers});
  public jwtHelper: JwtHelper = new JwtHelper();

  constructor(private http: Http, private authH: AuthHttp) { }

  loggedIn(): Boolean {
    return tokenNotExpired();
  }

  login(user: User) {
    const body = '{"username":"' + user.username + '","password":"' + user.password + '"}';
    return this.http.post(this.api + 'authenticate', body, this.options).map(res => {
      const token = res.json().token;
      const username = res.json().username;
      if (token) {
        localStorage.setItem('token', token);
        const decodedToken = this.jwtHelper.decodeToken(token);
        localStorage.setItem('username', decodedToken.data.username);
      }
      return res.json();
    });
  }

  logout() {
    localStorage.clear();
  }

  getUsers() {
    return this.authH.get(this.api).map(res => res.json());
  }

  createUser(user: User) {
    return this.http.post(this.api + 'register', user, this.options).map(res => res.json());
  }
}



