import { Component, OnInit } from '@angular/core';
import { UsersService } from '../services/users.service';
import { User } from '../interfaces';
import { Router } from '@angular/router';
import { NgModel, Validators } from '@angular/forms';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  constructor(private service: UsersService, private router: Router) { }
  user: User;
  confirmPassword: String;
  msg: String;

  ngOnInit() {
    this.user = {
      username: '',
      password: '',
      email: '',
      address: {
        street: '',
        city: '',
        state: '',
        zip_code: null
      }
    };
    this.confirmPassword = '';
  }

  register() {
    if (this.user.username === '' || this.user.password === '' || this.confirmPassword === '' || this.user.address === null) {
      return this.msg = 'Please fill in all fields';
    }
    if (this.user.password !== this.confirmPassword) {
      return this.msg = 'Passwords do not match';
    }
    this.service.createUser(this.user).subscribe(res => {
      if (res.success === false) {
        return this.msg = res.msg;
      }
      this.router.navigate(['/login']);
    });
  }

}



