import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { RequestOptions, Headers } from '@angular/http';

import { AuthHttp, tokenNotExpired, JwtHelper } from 'angular2-jwt';


import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs/observable';

import { Product, Cart } from '../interfaces';

@Injectable()
export class ProductsService {

  public api = 'products/';
  private headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded'});
  private options = new RequestOptions({headers: this.headers});

  private costTotal: number;
  public costString: string;
  public cart: Cart;

  constructor(private auth: AuthHttp, private http: Http) {
    this.costTotal = 0.00;
    this.costString = '';
    this.cart = {
      products: []
    };
  }

  getProducts() {
    return this.http.get(this.api).map(res => res.json());
  }

  getProduct(_id: any) {
    return this.http.get(this.api + _id).map(res => res.json());
  }

  calculateCart(cost: number, product: Product) {
    const total = this.costTotal + cost;

    if (product != null) {
      // const newProduct = Object.assign({}, product);
      const newProduct = {... product};
      this.cart.products.push(newProduct);
    }

    this.costTotal = total;
    this.costString = total.toString();
    const decimalPos = this.costString.indexOf('.');

    if (decimalPos < 0) {
      return this.costString.concat('.', '00');
    }

    return this.costString;
  }

  clearCart() {
    this.costTotal = 0;
    this.costString = '';
    this.cart.products = [];
  }

}



