import { Component, OnInit } from '@angular/core';
import { NgModel } from '@angular/forms';
import { Router } from '@angular/router';
import { ProductsService } from '../services/products.service';
import { Product } from '../interfaces';

@Component({
  selector: 'app-shop',
  templateUrl: './shop.component.html',
  styleUrls: ['./shop.component.css']
})
export class ShopComponent implements OnInit {

  constructor(private productsService: ProductsService, public router: Router) { }
  products: Product[];
  product_names: string[];
  productId: any;
  sorting = 'name:a-z';

  ngOnInit() {
    this.getProducts();
  }

  getProducts() {
    this.productsService.getProducts().subscribe(products => {
      this.products = products;
      this.sortProducts();
    });
  }

  sortProducts() {
    switch (this.sorting) {
      case 'name:a-z' : this.products.sort((a: any, b: any) => {
        const x  = a.product_name.toLowerCase();
        const y = b.product_name.toLowerCase();
        if (x < y) { return -1; }
        if (x > y) { return 1; }
        return 0;
      }); break;
      case 'name:z-a' : this.products.sort((a: any, b: any) => {
        const x  = a.product_name.toLowerCase();
        const y = b.product_name.toLowerCase();
        if (x > y) { return -1; }
        if (x < y) { return 1; }
        return 0;
      }); break;
    case 'price:l-h' : this.products.sort(function(a: any, b: any){return a.price - b.price; }); break;
    case 'price:h-l' : this.products.sort(function(a: any, b: any){return b.price - a.price; }); break;
    }
  }

  gotoProduct(id: string) {
    this.router.navigate(['product', id]);
  }

}



