import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { NgModel } from '@angular/forms';
import { ProductsService } from '../services/products.service';
import { Product } from '../interfaces';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
  product: Product;
  private added: boolean;
  msg: string;

  constructor(private route: ActivatedRoute, private router: Router, private service: ProductsService) { }

  ngOnInit() {
    this.product = {
      product_name: '',
      price: 0,
      colors: [],
      brand: '',
      section: '',
      img_path: '',
      size: 0
    };

    this.added = false;
    this.getProduct();
  }

  getProduct() {
    this.route.paramMap.switchMap((params: ParamMap) => this.service.getProduct(params.get('id'))).subscribe(product => {
      this.product = {
        product_name: product.product_name,
        price: product.price,
        colors: product.colors,
        brand: product.brand,
        section: product.section,
        img_path: product.img_path,
        size: 0
      };
    });
  }

  addProduct(product: Product) {
    if (product.size === 0) {
      this.added = false;
      this.msg = 'Please choose a size.';
      setTimeout( () => {
        this.msg = null;
      }, 2500);
    } else {
      this.service.calculateCart(product.price, product);
      this.added = true;
    }
  }
}



